import React from 'react';
import { Button, Grid, ButtonGroup, ListItemText } from '@mui/material';
import Typography from '@mui/material/Typography';
import dayjs from 'dayjs';
import { AddToAppleCalendarButton, AddToGoogleCalendarButton } from './component/AddCalendar';

export default function AppointmentConfirmation(
    this: any,
    props: {
        firstName: string,
        lastName: string,
        appointmentStartTime: Date,
        appointmentEndTime: Date,
        appointmentScheduleName: string,
        appointmentLocation: string,
        appointmentDetails: string,
        phone: string,
        selectedServices: string[],
        selectedSchedule: number | null
        selectedScheduleEndTime: number | null
    }
) {
    const { firstName, lastName, appointmentStartTime, appointmentEndTime, appointmentScheduleName, appointmentLocation, appointmentDetails, phone, selectedServices, selectedSchedule, selectedScheduleEndTime } = props;


    const datas = [
        { name: '', detail: lastName + firstName },
        { name: '', detail: phone },
        { name: '', detail: dayjs(selectedSchedule).format('YYYY  MM  DD ') },
        { name: '', detail: dayjs(selectedSchedule).format('A h  m ') },
        { name: '', detail: selectedServices.join(', ') },
        { name: '', detail: '101()' },
    ];


    return (
        <Grid>
            {/* <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
                
            </Typography> */}
            <Grid container alignItems="center" justifyContent="center">
                {datas.map((datas) => (
                    <React.Fragment key={datas.name}>
                        <Grid item md={3} xs={4}>
                            <Typography gutterBottom>{datas.name}</Typography>
                        </Grid>
                        <Grid item md={7} xs={8}>
                            <Typography gutterBottom>{datas.detail}</Typography>
                        </Grid>
                    </React.Fragment>
                ))}
            </Grid>
            <br />

            <Grid container spacing={2} justifyContent="center">
                <Grid item md={5} xs={6}>
                    <ButtonGroup
                        sx={{ "& > button": { margin: "2px" } }}
                        fullWidth
                        variant="outlined"
                    >
                        <AddToGoogleCalendarButton
                            appointmentStartTime={appointmentStartTime}
                            appointmentEndTime={appointmentEndTime}
                            appointmentScheduleName={appointmentScheduleName}
                            appointmentLocation={appointmentLocation}
                            appointmentDetails={appointmentDetails}
                        />
                    </ButtonGroup>
                </Grid>
                <Grid item md={5} xs={6}>
                    <ButtonGroup
                        sx={{ "& > button": { margin: "2px" } }}
                        fullWidth
                        variant="outlined"
                    >
                        <AddToAppleCalendarButton
                            appointmentStartTime={appointmentStartTime}
                            appointmentEndTime={appointmentEndTime}
                            appointmentScheduleName={appointmentScheduleName}
                            appointmentLocation={appointmentLocation}
                            appointmentDetails={appointmentDetails}
                        />
                    </ButtonGroup>
                </Grid>
            </Grid>
            <br />

            <Grid container alignItems="center" justifyContent="center">
                <Grid item md={10} xs={12}>
                    <Typography gutterBottom>
                        
                    </Typography>
                </Grid>
                <Grid item md={10} xs={12}>
                    <ol>
                        <li>
                            <Typography gutterBottom> <br />
                                 <br />
                                </Typography>
                        </li>
                        <li>
                            <Typography gutterBottom></Typography>
                        </li>
                        <li>
                            <Typography gutterBottom></Typography>
                        </li>
                        <li>
                            <Typography gutterBottom></Typography>
                        </li>
                        <li>
                            <Typography gutterBottom></Typography>
                        </li>
                    </ol>
                </Grid>
            </Grid>
        </Grid>
    );
};
