import * as React from 'react';
import Typography from '@mui/material/Typography';
import { Grid, Button } from '@mui/material';
import dayjs, { Dayjs } from 'dayjs';
import { myPostBooking } from './api/postBooking';


export default function Review(
  this: any,
  props: {
    firstName: string,
    lastName: string,
    gender: string,
    selectedBranchId: string,
    selectedSchedule: number | null,
    selectedServices: string[],
    phone: string
  }
) {
  const { firstName, lastName, gender, selectedBranchId, selectedSchedule, selectedServices, phone } = props;

  const datas = [
    { name: '', detail: lastName + firstName },
    { name: '', detail: phone },
    { name: '', detail: dayjs(selectedSchedule).format('YYYY  MM  DD ') },
    { name: '', detail: dayjs(selectedSchedule).format('A h  m ') },
    { name: '', detail: selectedServices.join(', ') },
  ];

  return (
    <React.Fragment>
        <Grid container alignItems="center" justifyContent="center">
          {datas.map((datas) => (
            <React.Fragment key={datas.name}>
              <Grid item md={3} xs={4}>
                <Typography gutterBottom>{datas.name}</Typography>
              </Grid>
              <Grid item md={7} xs={8}>
                <Typography gutterBottom>{datas.detail}</Typography>
              </Grid>
            </React.Fragment>
          ))}
      </Grid>
    </React.Fragment>
  );
}
